import L from 'leaflet';

const initMap = () => {
  // HTML
  const container = document.querySelector('.contacts__map');

  // Constants
  const coordinates = {
    lat: 59.95830,
    lng: 30.31748,
  }

  const markerCoodinates = {
    lat: 59.96831,
    lng: 30.31748
  }

  const baloonSettings = {
    iconUrl: './images/map/pin.svg',
    iconSize: [52, 52],
    iconAnchor: [26, 52],
  }

  // OpenStreetMaps
  const openStreetMap = {
    url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
    settings: {
      attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
    },
  };

  // Leaflet
  const map = L.map(container); // Init map

  map.setView(coordinates, 12); // Set coordinates and zoom
  L.tileLayer(openStreetMap.url, openStreetMap.settings).addTo(map); // Set map layers
  const baloonIcon = L.icon(baloonSettings); // Set baloon icon
  const marker = L.marker(markerCoodinates, {draggable: false, icon: baloonIcon}) // Set baloon with icon
  marker.addTo(map) // Add baloon on the map
}

initMap();