const nav = document.querySelector('.nav');
const button = document.querySelector('.header__burger');

const classes = {
  openedButton: 'header__burger--opened',
  openedNav: 'nav--opened',
}

const openNav = () => {
  button.classList.toggle(classes.openedButton);
  nav.classList.toggle(classes.openedNav);
}

const ariaHandler = () => {
  if (button.classList.contains(classes.openedButton)) {
    button.setAttribute('aria-label', 'Закрыть меню.')
  } else {
    button.setAttribute('aria-label', 'Открыть меню.')
  }
} 

button.addEventListener('click', () => {
  openNav();
  ariaHandler();
})